<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <!-- <el-form-item label="车牌号">
                    <el-input v-model.trim="filters.movieName" placeholder="请输入车牌号"></el-input>
                </el-form-item>
                <el-form-item label="车主名称">
                    <el-input v-model.trim="filters.movieName" placeholder="请输入车主名称"></el-input>
                </el-form-item>
                <el-form-item label="工单状态">
                    <el-select v-model="filters.value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button>查询</el-button> -->
                <el-button type="primary" icon="plus" @click="chooseCar">新建</el-button>
                <!-- <el-button type="primary" icon="plus">导出</el-button> -->
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="plate_number" label="车牌号" width="120">
                </el-table-column>
                <el-table-column prop="vin_number" label="vin号" width="120">
                </el-table-column>
                <el-table-column prop="band_name" label="业务发起车主" width="120">
                </el-table-column>
                <el-table-column prop="change_user_name" label="过户车主" width="120">
                </el-table-column>
                <el-table-column prop="work_status_name" label="工单状态" width="120">
                </el-table-column>
                <el-table-column prop="operater" label="当前处理人" width="120">
                </el-table-column>
                <el-table-column prop="create_time" label="创建日期">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button
                            type="text"
                            icon="el-icon-view"
                            @click="$router.push({path: '/anomalyCarChange/view', query: { id: row.id }})">
                            查看
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/anomalyCarChange/edit', query: { id: row.id }})"
                            v-if="isShowEdit(row)">
                            修改
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="$router.push({path: '/anomalyCarChange/edit', query: { id: row.id }})"
                            v-if="isShowAudit(row)">
                            审核
                        </el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            @click="handleDelete(row, $index)"
                            v-if="isShowDel(row)">
                            删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination layout="prev, pager, next, jumper" @current-change="handleCurrentChange" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
        
        <!-- 选择车辆 -->
        <el-dialog title="选择车辆" :visible.sync="dialogSelectCarType" width="50%">
            <selectCar v-if="hackReset" :selectCarOptions="selectCarOptions" @emitCarSubmit="emitCarSubmit" @emitCarClose="emitCarClose"></selectCar>
        </el-dialog>

        <!-- 选择过户车主 -->
        <el-dialog title="选择过户车主" :visible.sync="dialogSelectClient" width="50%">
            <selectClient v-if="hackResetClient" :selectClientOptions="selectClientOptions" @emitClientSubmit="emitClientSubmit" @emitClientClose="emitClientClose" @emitClientGoback="emitClientGoback"></selectClient>
        </el-dialog>
    </div>
</template>

<script>
    import * as API from '@/api/trafficManagement'
    import selectCar from '@/components/selectCar'
    import selectClient from '@/components/selectClient'
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                total: 1,
                limit: 10,
                loading: true,
                bus_id: '',
                hackReset: true,
                hackResetClient: true,
                dialogSelectCarType: false,
                selectCarOptions:{
                    tableConfig:{
                        plate_number: true,
                        vin_number: true,
                        band_name: true,
                        car_info: true
                    },
                    formConfig: {
                        type: true,
                        brand: true,
                        series: true,
                        plate_number: true,
                    },
                    pageType: 'insuranceOrder'
                    
                },
                dialogSelectClient: false,
                selectClientOptions:{
                    pageType: 'people',
                    goback: true
                },
                filters: {
                    movieID:'',
                    movieName:'',
                    value:'3'
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }]
            }
        },
        components:{
            selectCar,
            selectClient
        },
        created() {
            this.getData();
        },
        computed: {

        },
        methods: {
            isShowEdit(row){
                if(row.node_id==232 || row.node_id==0){
                    return row.handle==1
                }
            },
            isShowAudit(row){
                return row.node_id!=232 && row.auth_user==1
            },
            isShowDel(row){
                return row.node_id==232 || row.node_id==0
            },
            // 选择车辆
            chooseCar(data){
                this.dialogSelectCarType = true
                this.hackReset = false
                this.$nextTick(() => {
                    this.hackReset = true
                })
            },
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params= {
                    page: this.page,
                    ...this.filters
                }
                this.loading=true
                API.apiAnomalyCarChangeList(params).then(res => {
                    this.loading=false
                    if(res.code == 0){
                        this.tableData = res.data;
                        this.total = res.total;
                    }else{
                        this.$message.error(res.msg)
                    }
                })
                
            },
            search() {
                this.isSearch = true;
            },
            handleEdit(row, index) {

            },
            handleDelete(row, index) {
                API.apiAnomalyCarChangeDel({id: row.id, operation_uniqid:row.operation_uniqid}).then(res => {
                    if(res.code == 0){
                        this.tableData.splice(index, 1)
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            emitCarClose(){
                this.dialogSelectCarType = false;
            },
            emitCarSubmit(row){
                this.bus_id = row.id,
                this.dialogSelectClient = true;
            },
            emitClientGoback(){
                this.dialogSelectClient = false;
            },
            emitClientClose(){
                this.dialogSelectCarType = false;
                this.dialogSelectClient = false;
            },
            emitClientSubmit(row){
                let params = {
                    bus_id: this.bus_id,
                    ...row
                }
                API.apiAnomalyCarChangeAdd(params).then(res => {
                    if(res.code == 0){
                        this.dialogSelectClient = false;
                        this.dialogSelectCarType = false;
                        this.getData();
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            }
        }
    }

</script>

<style scoped>

</style>
